// src/pages/ManageContentPage.js
import React, { useState, useCallback, useMemo } from 'react';
import { useTestData } from '../context/TestDataContext'; // Only testData is in this context now
import ManagedSubjectSection from '../components/ManagedSubjectSection';
import { useHandlers } from '../context/HandlerContext';
import AddModal from '../components/AddModal'; // Import the new modal

const ManageContentPage = React.memo(({ onBackToProfile, showNotification }) => {
  const { testData } = useTestData();
  const handlers = useHandlers();
  const [expandedSubjects, setExpandedSubjects] = useState({});
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalConfig, setModalConfig] = useState({ title: '', fields: [], onSave: () => {} });

  const subjects = useMemo(() => Object.keys(testData), [testData]);

  const handleToggleExpand = useCallback((subjectName) => {
    setExpandedSubjects((prev) => ({
      ...prev,
      [subjectName]: !prev[subjectName],
    }));
  }, []);

  const handleDeleteSubject = React.useCallback(
    (subjectName) => {
      handlers.deleteSubject(subjectName);
    },
    [handlers]
  );

  const handleDeleteTest = React.useCallback(
    (subject, title) => {
      handlers.deleteTest(subject, title);
    },
    [handlers]
  );

  // Memoized onSave functions for modal configs
  const handleAddSubjectSave = useCallback(
    (data) => {
      handlers.addSubject(data.subjectName);
      setExpandedSubjects((prev) => ({ ...prev, [data.subjectName]: true }));
    },
    [handlers]
  );

  const handleAddTestSave = useCallback(
    (data) => {
      handlers.addTest(data.subject, data.title, data.mcqs, data.date, data.link);
      setExpandedSubjects((prev) => ({ ...prev, [data.subject]: true }));
    },
    [handlers]
  );

  // Memoized modal openers
  const openAddSubjectModal = useCallback(() => {
    setModalConfig({
      title: 'Add New Subject',
      fields: [
        {
          name: 'subjectName',
          label: 'Subject Name',
          placeholder: 'e.g., Biochemistry',
          required: true,
        },
      ],
      onSave: handleAddSubjectSave,
    });
    setIsModalOpen(true);
  }, [handleAddSubjectSave]);

  const openAddTestModal = useCallback(() => {
    setModalConfig({
      title: 'Add New Test',
      fields: [
        {
          name: 'subject',
          label: 'Select Subject',
          type: 'select',
          required: true,
          options: subjects.map((s) => ({ value: s, label: s })),
          defaultValue: subjects[0] || '',
        },
        { name: 'title', label: 'Test Title', placeholder: 'e.g., Final Exam', required: true },
        {
          name: 'mcqs',
          label: 'Number of MCQs',
          type: 'number',
          placeholder: 'e.g., 50',
          required: true,
        },
        { name: 'date', label: 'Date', type: 'date', required: true },
        {
          name: 'link',
          label: 'Test Link (Optional)',
          type: 'url',
          placeholder: 'https://example.com/test',
        },
      ],
      onSave: handleAddTestSave,
    });
    setIsModalOpen(true);
  }, [handleAddTestSave, subjects]);

  // Empty state component
  const EmptyState = () => (
    <div className="flex flex-col items-center justify-center py-12 text-center">
      <div className="w-24 h-24 mb-4 text-gray-300">
        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253" />
        </svg>
      </div>
      <h3 className="text-lg font-medium text-gray-900">No subjects added yet</h3>
      <p className="mt-2 text-sm text-gray-500">Start by adding your first subject to organize your tests.</p>
      <button
        onClick={openAddSubjectModal}
        className="mt-4 px-4 py-2 bg-blue-600 text-white text-sm font-medium rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
      >
        Add Subject
      </button>
    </div>
  );

  return (
    // All subject/test handlers now come from context
    <>
      <div id="page-manage-tests" className="page px-6 py-8">
        <header className="flex items-center justify-between mb-6">
          <h1 className="text-3xl font-bold text-gray-900">Manage Content</h1>
          <button
            onClick={onBackToProfile}
            className="text-blue-600 hover:text-blue-800 text-sm font-medium"
          >
            Done
          </button>
        </header>

        <div className="space-y-4 mb-6">
          <button
            onClick={openAddSubjectModal}
            className="w-full bg-green-50 text-green-700 font-semibold py-2 px-4 rounded-lg flex items-center justify-center hover:bg-green-100 transition duration-300"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="mr-2"
            >
              <line x1="12" y1="5" x2="12" y2="19"></line>
              <line x1="5" y1="12" x2="19" y2="12"></line>
            </svg>
            Add New Subject
          </button>

          <button
            onClick={openAddTestModal}
            className="w-full bg-blue-50 text-blue-700 font-semibold py-2 px-4 rounded-lg flex items-center justify-center hover:bg-blue-100 transition duration-300"
            disabled={subjects.length === 0}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="mr-2"
            >
              <line x1="12" y1="5" x2="12" y2="19"></line>
              <line x1="5" y1="12" x2="19" y2="12"></line>
            </svg>
            Add New Test
          </button>
        </div>

        <h2 className="text-xl font-bold text-gray-800 mb-4">Existing Content</h2>
        <div id="manage-content-list" className="space-y-4">
          {subjects.length > 0 ? (
            subjects.map((subject) => (
              <div key={subject} className="space-y-4">
                <ManagedSubjectSection
                  subjectName={subject}
                  tests={testData[subject]}
                  onDeleteSubject={handleDeleteSubject}
                  onDeleteTest={handleDeleteTest}
                  isExpanded={expandedSubjects[subject]}
                  onToggleExpand={handleToggleExpand}
                />
                {expandedSubjects[subject] && testData[subject].length === 0 && (
                  <div className="bg-gray-50 rounded-lg p-6 text-center">
                    <p className="text-gray-500">No tests in this subject yet.</p>
                    <button
                      onClick={openAddTestModal}
                      className="mt-2 text-sm text-blue-600 hover:text-blue-800 font-medium"
                    >
                      Add your first test
                    </button>
                  </div>
                )}
              </div>
            ))
          ) : (
            <EmptyState />
          )}
        </div>
      </div>

      <AddModal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onSave={modalConfig.onSave}
        title={modalConfig.title}
        fields={modalConfig.fields}
        showNotification={showNotification}
      />
    </>
  );
});

export default ManageContentPage;
